.hide {
	display: none; }

.mark {
	color: $mark-color;
	font-style: inherit;
	font-weight: inherit; }

.text-light {
	color: $text-color-light; }

.align-center {
	text-align: center; }

.align-left {
	text-align: left; }

.align-right {
	text-align: right; }

.float-left {
	float: left; }

.float-right {
	float: right; }

.nobr {
	white-space: nowrap; }

.hide-text {
	@include hide-text; }

/* content img */

.layout-article {
	& > img, .imgc {
		text-align: center;
		display: block;
		margin: 0 auto $bottom-space;
		width: $flood-layout-width;
		max-width: 100%;
		clear: both; }

	.imgl {
		text-align: left;
		float: left;
		margin: 0 $bottom-space $bottom-space 0; }

	.imgr {
		text-align: right;
		float: right;
		margin: 0 0 $bottom-space $bottom-space; }

	.imgl, .imgr {
		max-width: 40%;

		@include media('<tab-sm') {
			float: none;
			display: block;
			margin-left: auto;
			margin-right: auto;
			max-width: 100%; } } }

.imgc, .imgl, .imgr {
	figcaption {
		display: block;
		padding: $bottom-space 0 0;
		margin: 0;
		@include font($font-size--small, $line-height--small); } }

/* content img end */

/* columnize */
.columnize, .columnize--3, .columnize--4 {
	column-count: 2;
	column-gap: 2em;
	margin-bottom: $bottom-space;
	font-size: 0.9em; }

.columnize--3 {
	column-count: 3;
	font-size: 0.85em; }

.columnize--4 {
	column-count: 4;
	font-size: 0.8em;

	@include media('<desk-sm') {
		column-count: 3; } }

.columnize--3, .columnize--4 {

	@include media('<tab-lg') {
		column-count: 2; } }

.columnize, .columnize--3, .columnize--4 {

	@include media('<tab-sm') {
		column-count: 1; } }

/* columnize end */

/* columns */
$columns-gap: 2em;
.columns {
	@include clear; }

.column {
	float: left;
	width: calc(50% - #{round($columns-gap/2)});
	margin: 0 round($columns-gap/2);

	@include media('<mob-lg') {
		float: none;
		width: auto;
		margin: 0 0 5rem;

		&:last-child {
			margin-bottom: 0; } }

	&:nth-child(odd) {
		margin-left: 0; }

	&:nth-child(even) {
		margin-right: 0; } }

/* columns end */

.#{$user-content} {
	@each $tags in h2, h3, h4, h5 {
		p + #{$tags},
		ul + #{$tags},
		ol + #{$tags} {
			margin-top: round-float($bottom-space--article, 1); } } }

/* list */
.#{$user-content} {
	//list
	ul, ol {
		margin: 0 0 $bottom-space;
		padding: 0;
		list-style: none;

		li {
			padding-left: $list-space;
			margin: 0 0 round($bottom-space*0.5);
			position: relative; } }

	ul > li::before {
		content: '';
		display: block;
		float: left;
		@include size(0);
		margin: 0.4em 0 -0.5em -#{$list-space};
		border: solid transparent {
			width: 5px 0 5px 9px;
			left-color: $base-color; } }

	ol {
		counter-reset: section;

		& > li {
			counter-increment: section;
			height: 1%;
			overflow: hidden;

			&::before {
				position: relative;
				display: inline-block;
				text-align: left;
				@include font-alt-bold;
				color: $base-color;
				content: counter(section);
				padding: 0 4px 0 0;
				min-width: $list-space;
				margin-left: -#{$list-space}; } } }

	ul, ol {
		ul, ol {
			margin: round($bottom-space*0.5) 0; } } }

/* list reset */
.reset-list,
.#{$user-content} .reset-list {
	list-style: none;

	li {
		&, &:first-child {
			padding: 0;

			&::before {
				content: none; } } } }

/* list reset end */
/* list end */

/* table */
table {
	border-collapse: collapse;
	border-spacing: 0;
	width: auto;

	td, th {
		@include font($font-size, $line-height);
		color: inherit;
		padding: 8px 15px;
		border-color: $text-color; } }

.table-auto {
	position: relative;
	overflow: auto;
	max-width: 100%;
	-webkit-overflow-scrolling: touch;
	margin-bottom: $bottom-space;

	table {
		margin-bottom: 0;
		width: auto;
		min-width: 100%; } }

.#{$user-content} {
	table {
		margin-bottom: $bottom-space; }

	.table-auto table {
		margin-bottom: 0; }

	.tbl {
		border: none;

		td, th {
			color: $text-color;
			border: 0 solid $base-color;
			@include font(1.4rem, 1.5);

			&:first-child, &:first-child {
				border-left-width: 1px; }

			&:last-child, &:last-child {
				border-right-width: 1px; } }

		tr:last-child {
			td {
				border-bottom-width: 0; } }

		td {
			border-top-width: 1px; }

		tbody tr:first-child td {
			border-top-width: 0; }

		tbody tr:nth-child(even) td {
			background: $base-color;
			color: white; }

		th, thead td {
			@include font-bold;
			font-size: 1.6rem;
			text-align: left; }

		th, thead td, tfoot td {
			background: $base-color;
			color: white;
			border-color: $base-color;
			border-top-width: 0; } } }

/* table end */

/* blockquote */
.#{$user-content} {

	blockquote {
		display: block;
		margin: 0 0 $bottom-space;
		padding: 0;
		background-color: white;

		q {
			display: block;
			margin: 0;
			padding: 0 0 10px;
			@include font-alt;
			@include font(round-float($font-size*1.25, 1), $line-height);
			color: $text-color;
			text-align: center;

			&::before, &::after {
				@include pseudo($pos: relative);
				@include font(4rem, 1);
				text-align: left; }

			&::before {
				content: '«';
				padding-left: 17%;
				padding-bottom: 10px; }

			&::after {
				content: '»';
				text-align: right;
				padding-right: 18.5%;
				padding-top: 8px; } }

		cite {
			text-align: right;
			display: block;
			margin-left: auto;
			@include font($font-size, $line-height);
			color: $text-color-light; } } }

/* blockquote end */

/* user spacer */
.user-spacer {
	display: block;
	height: $bottom-space--section;
	@include font(0, 0);
	@include clear;

	@include media('<desk-lg') {
		height: $bottom-space--element; } }

/* user spacer end */

/* datetime */
.datetime {
	display: block;
	font-style: normal;
	margin-bottom: $bottom-space; }

/* datetime end */

/* view print */
.view-print {
	@include display-inline-block-top;
	padding: 6px 12px;
	margin-bottom: $bottom-space;
	background: #f9f9f9;
	line-height: 1;
	color: black;
	text-decoration: none;

	@include media('<desk-lg') {
		margin-bottom: $bottom-space--element; }

	@include media('<tab-lg') {
		display: none; }

	&:last-child {
		margin-bottom: 0; }

	.no-touchevents &:hover {
		background-color: #eaeaea; }

	svg {
		display: inline-block;
		vertical-align: middle;
		width: 20px;
		height: 20px;
		margin-right: 5px; }

	span {
		display: inline-block;
		vertical-align: middle; } }

/* view print end */
