h1 {
	font-size: 4.8rem;
	line-height: 1.25;
}

@media (max-width: 991px) {
	h1 {
		font-size: 3.6rem;
		line-height: 1.33;
	}
}

@media (max-width: 639px) {
	h1 {
		font-size: 3rem;
		line-height: 1.4;
	}
}

h2 {
	font-size: 3.6rem;
	line-height: 1.33;
}

@media (max-width: 991px) {
	h2 {
		font-size: 3rem;
		line-height: 1.36;
	}
}

@media (max-width: 639px) {
	h2 {
		font-size: 2.6rem;
		line-height: 1.41;
	}
}

h3 {
	font-size: 2.4rem;
	line-height: 1.5;
}

@media (max-width: 639px) {
	h3 {
		font-size: 2.2rem;
		line-height: 1.47;
	}
}

h4 {
	font-size: 1.8rem;
	line-height: 1.67;
}

h5 {
	font-size: 1.8rem;
	line-height: 1.67;
}

h6 {
	font-size: 1.4rem;
	line-height: 2.09;
	text-transform: uppercase;
}

p {
	padding: 0;
	margin: 0 0 3rem;
}

hr {
	line-height: 1px;
	height: 0;
	border: none;
	background: none;
	border-bottom: 1px solid #e6e6e6;
	margin-bottom: 3rem;
}

a {
	color: #282828;
	text-decoration: underline;
	cursor: default;
}

a:link {
	cursor: pointer;
}

.no-touchevents a:hover {
	text-decoration: none;
}

a[href^="tel"], .no-touchevents a[href^="tel"]:hover {
	text-decoration: none !important;
	color: inherit;
	cursor: default;
}

a[href^="javascript:"] {
	cursor: default;
}

img {
	border: none;
	vertical-align: top;
	max-width: 100%;
}

table img {
	max-width: none;
}

b, strong, th {
	font-family: "akrobat-bold", Helvetica, Arial, sans-serif;
	font-weight: normal;
	font-style: normal;
}

i, em {
	font-style: italic;
}

i b, i strong, em b, em strong, b i, b em, strong i, strong em {
	font-weight: bold;
	font-style: italic;
}

.hide {
	display: none;
}

.mark {
	color: #ee1c25;
	font-style: inherit;
	font-weight: inherit;
}

.text-light {
	color: #b2b2b2;
}

.align-center {
	text-align: center;
}

.align-left {
	text-align: left;
}

.align-right {
	text-align: right;
}

.float-left {
	float: left;
}

.float-right {
	float: right;
}

.nobr {
	white-space: nowrap;
}

.hide-text {
	border: 0;
	clip: rect(0 0 0 0);
	height: 1px;
	margin: -1px;
	overflow: hidden;
	padding: 0;
	position: absolute;
	width: 1px;
}

.hide-text:active, .hide-text:focus {
	clip: auto;
	height: auto;
	margin: 0;
	overflow: visible;
	position: static;
	width: auto;
}

/* content img */
.layout-article > img, .layout-article .imgc {
	text-align: center;
	display: block;
	margin: 0 auto 3rem;
	width: 1440px;
	max-width: 100%;
	clear: both;
}

.layout-article .imgl {
	text-align: left;
	float: left;
	margin: 0 3rem 3rem 0;
}

.layout-article .imgr {
	text-align: right;
	float: right;
	margin: 0 0 3rem 3rem;
}

.layout-article .imgl, .layout-article .imgr {
	max-width: 40%;
}

@media (max-width: 767px) {
	.layout-article .imgl, .layout-article .imgr {
		float: none;
		display: block;
		margin-left: auto;
		margin-right: auto;
		max-width: 100%;
	}
}

.imgc figcaption, .imgl figcaption, .imgr figcaption {
	display: block;
	padding: 3rem 0 0;
	margin: 0;
	font-size: 1.4rem;
	line-height: 1.71;
}

/* content img end */
/* columnize */
.columnize, .columnize--3, .columnize--4 {
	column-count: 2;
	column-gap: 2em;
	margin-bottom: 3rem;
	font-size: 0.9em;
}

.columnize--3 {
	column-count: 3;
	font-size: 0.85em;
}

.columnize--4 {
	column-count: 4;
	font-size: 0.8em;
}

@media (max-width: 1199px) {
	.columnize--4 {
		column-count: 3;
	}
}

@media (max-width: 991px) {
	.columnize--3, .columnize--4 {
		column-count: 2;
	}
}

@media (max-width: 767px) {
	.columnize, .columnize--3, .columnize--4 {
		column-count: 1;
	}
}

/* columnize end */
/* columns */
.columns::after {
	content: '';
	display: table;
	clear: both;
}

.column {
	float: left;
	width: calc(50% - 1em);
	margin: 0 1em;
}

@media (max-width: 639px) {
	.column {
		float: none;
		width: auto;
		margin: 0 0 5rem;
	}
	.column:last-child {
		margin-bottom: 0;
	}
}

.column:nth-child(odd) {
	margin-left: 0;
}

.column:nth-child(even) {
	margin-right: 0;
}

/* columns end */
.user-content p + h2,
.user-content ul + h2,
.user-content ol + h2 {
	margin-top: 5rem;
}

.user-content p + h3,
.user-content ul + h3,
.user-content ol + h3 {
	margin-top: 5rem;
}

.user-content p + h4,
.user-content ul + h4,
.user-content ol + h4 {
	margin-top: 5rem;
}

.user-content p + h5,
.user-content ul + h5,
.user-content ol + h5 {
	margin-top: 5rem;
}

/* list */
.user-content ul, .user-content ol {
	margin: 0 0 3rem;
	padding: 0;
	list-style: none;
}

.user-content ul li, .user-content ol li {
	padding-left: 30px;
	margin: 0 0 2rem;
	position: relative;
}

.user-content ul > li::before {
	content: '';
	display: block;
	float: left;
	width: 0;
	height: 0;
	margin: 0.4em 0 -0.5em -30px;
	border: solid transparent;
	border-width: 5px 0 5px 9px;
	border-left-color: #ee1c25;
}

.user-content ol {
	counter-reset: section;
}

.user-content ol > li {
	counter-increment: section;
	height: 1%;
	overflow: hidden;
}

.user-content ol > li::before {
	position: relative;
	display: inline-block;
	text-align: left;
	font-family: "akrobat-bold", Helvetica, Arial, sans-serif;
	font-weight: normal;
	font-style: normal;
	color: #ee1c25;
	content: counter(section);
	padding: 0 4px 0 0;
	min-width: 30px;
	margin-left: -30px;
}

.user-content ul ul, .user-content ul ol, .user-content ol ul, .user-content ol ol {
	margin: 2rem 0;
}

/* list reset */
.reset-list,
.user-content .reset-list {
	list-style: none;
}

.reset-list li, .reset-list li:first-child,
.user-content .reset-list li,
.user-content .reset-list li:first-child {
	padding: 0;
}

.reset-list li::before, .reset-list li:first-child::before,
.user-content .reset-list li::before,
.user-content .reset-list li:first-child::before {
	content: none;
}

/* list reset end */
/* list end */
/* table */
table {
	border-collapse: collapse;
	border-spacing: 0;
	width: auto;
}

table td, table th {
	font-size: 1.8rem;
	line-height: 1.67;
	color: inherit;
	padding: 8px 15px;
	border-color: #282828;
}

.table-auto {
	position: relative;
	overflow: auto;
	max-width: 100%;
	-webkit-overflow-scrolling: touch;
	margin-bottom: 3rem;
}

.table-auto table {
	margin-bottom: 0;
	width: auto;
	min-width: 100%;
}

.user-content table {
	margin-bottom: 3rem;
}

.user-content .table-auto table {
	margin-bottom: 0;
}

.user-content .tbl {
	border: none;
}

.user-content .tbl td, .user-content .tbl th {
	color: #282828;
	border: 0 solid #ee1c25;
	font-size: 1.4rem;
	line-height: 1.5;
}

.user-content .tbl td:first-child, .user-content .tbl td:first-child, .user-content .tbl th:first-child, .user-content .tbl th:first-child {
	border-left-width: 1px;
}

.user-content .tbl td:last-child, .user-content .tbl td:last-child, .user-content .tbl th:last-child, .user-content .tbl th:last-child {
	border-right-width: 1px;
}

.user-content .tbl tr:last-child td {
	border-bottom-width: 0;
}

.user-content .tbl td {
	border-top-width: 1px;
}

.user-content .tbl tbody tr:first-child td {
	border-top-width: 0;
}

.user-content .tbl tbody tr:nth-child(even) td {
	background: #ee1c25;
	color: white;
}

.user-content .tbl th, .user-content .tbl thead td {
	font-family: "akrobat-bold", Helvetica, Arial, sans-serif;
	font-weight: normal;
	font-style: normal;
	font-size: 1.6rem;
	text-align: left;
}

.user-content .tbl th, .user-content .tbl thead td, .user-content .tbl tfoot td {
	background: #ee1c25;
	color: white;
	border-color: #ee1c25;
	border-top-width: 0;
}

/* table end */
/* blockquote */
.user-content blockquote {
	display: block;
	margin: 0 0 3rem;
	padding: 0;
	background-color: white;
}

.user-content blockquote q {
	display: block;
	margin: 0;
	padding: 0 0 10px;
	font-family: "akrobat-regular", Helvetica, Arial, sans-serif;
	font-weight: normal;
	font-style: normal;
	font-size: 2.3rem;
	line-height: 1.67;
	color: #282828;
	text-align: center;
}

.user-content blockquote q::before, .user-content blockquote q::after {
	content: "";
	display: block;
	position: relative;
	font-size: 4rem;
	line-height: 1;
	text-align: left;
}

.user-content blockquote q::before {
	content: '«';
	padding-left: 17%;
	padding-bottom: 10px;
}

.user-content blockquote q::after {
	content: '»';
	text-align: right;
	padding-right: 18.5%;
	padding-top: 8px;
}

.user-content blockquote cite {
	text-align: right;
	display: block;
	margin-left: auto;
	font-size: 1.8rem;
	line-height: 1.67;
	color: #b2b2b2;
}

/* blockquote end */
/* user spacer */
.user-spacer {
	display: block;
	height: 8rem;
	font-size: 0;
	line-height: 0;
}

.user-spacer::after {
	content: '';
	display: table;
	clear: both;
}

@media (max-width: 1599px) {
	.user-spacer {
		height: 3rem;
	}
}

/* user spacer end */
/* datetime */
.datetime {
	display: block;
	font-style: normal;
	margin-bottom: 3rem;
}

/* datetime end */
/* view print */
.view-print {
	display: inline-block;
	vertical-align: top;
	padding: 6px 12px;
	margin-bottom: 3rem;
	background: #f9f9f9;
	line-height: 1;
	color: black;
	text-decoration: none;
}

@media (max-width: 1599px) {
	.view-print {
		margin-bottom: 3rem;
	}
}

@media (max-width: 991px) {
	.view-print {
		display: none;
	}
}

.view-print:last-child {
	margin-bottom: 0;
}

.no-touchevents .view-print:hover {
	background-color: #eaeaea;
}

.view-print svg {
	display: inline-block;
	vertical-align: middle;
	width: 20px;
	height: 20px;
	margin-right: 5px;
}

.view-print span {
	display: inline-block;
	vertical-align: middle;
}

/* view print end */
/* === BUTTON === */
[class|="btn"], [class*=" btn-"], input[type="submit"], input[type="button"], input[type="reset"], button {
	display: inline-block;
	vertical-align: top;
	position: relative;
	text-align: center;
	text-decoration: none;
	cursor: pointer;
}

[class|="btn"] input[type="submit"], [class|="btn"] input[type="button"], [class|="btn"] input[type="reset"], [class*=" btn-"] input[type="submit"], [class*=" btn-"] input[type="button"], [class*=" btn-"] input[type="reset"] {
	background: url("../img/empty.png") repeat;
	display: block;
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	padding: 0;
	border: none;
	z-index: 99;
	opacity: 0;
}

/* btn-default */
[class*="btn-default"] {
	position: relative;
	font-family: "akrobat-regular", Helvetica, Arial, sans-serif;
	font-weight: normal;
	font-style: normal;
	color: #282828;
	padding: 14px 26px;
