	width: 50%;
	position: relative;
}

@media (max-width: 639px) {
	.subs-form .form-fields {
		width: 100%;
	}
}

.subs-form .input-holder {
	position: relative;
	height: 50px;
	padding-right: 165px;
	will-change: opacity;
	transition-property: opacity;
	transition-duration: 0.25s;
}

.subs-form .input-holder input {
	height: 50px;
	font-size: 16px;
	line-height: 50px;
	padding: 0 20px;
	border-radius: 0;
}

.subs-form .input-holder input:-webkit-autofill {
	-webkit-box-shadow: 0 0 0 30px white inset;
}

.subs-form .input-holder input:-webkit-autofill {
	-webkit-text-fill-color: #282828;
}

.subs-form .form-success .input-holder {
	opacity: 0;
	visibility: hidden;
	transform: scale(0.76);
	transform: scale3d(0.76, 0.76, 1);
	pointer-events: none;
}

.subs-form input[type='submit'], .subs-form button {
	display: inline-block;
	vertical-align: top;
	height: 50px;
	width: 165px;
	min-width: 0;
	padding: 0 10px;
	position: absolute;
	top: 0;
	right: 0;
	border: none;
	font-family: "akrobat-bold", Helvetica, Arial, sans-serif;
	font-weight: normal;
	font-style: normal;
	font-size: 18px;
	line-height: 50px;
	letter-spacing: 0.1em;
	text-align: center;
	color: white;
	cursor: pointer;
	box-shadow: none;
	background-color: #ee1c25;
	border-radius: 0;
}

.subs-form .subs-success {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	opacity: 0;
	pointer-events: none;
	background-color: #ee1c25;
	transform: scale(0.76, 0.76) translateZ(0);
	border-radius: 0;
	text-align: center;
	color: white;
	font-size: 16px;
	line-height: 50px;
	transition-property: opacity, visibility, transform;
	transition-duration: 0.25s;
}

.subs-form .subs-success span {
	position: relative;
	display: inline-block;
	vertical-align: top;
}

.subs-form .subs-success span::before, .subs-form .subs-success span::after {
	content: "";
	display: block;
	position: absolute;
	width: 0;
	height: 2px;
	left: 0;
	top: 50%;
	background-color: currentColor;
	will-change: width;
	transition-property: width;
	transition-duration: 0.12s;
	transition-timing-function: linear;
}

.subs-form .subs-success span::before {
	margin: -3px 0 0 -29px;
	transform: rotate(45deg) translateZ(0);
	transform-origin: 0 8px;
	transition-delay: 0.12s;
}

.subs-form .subs-success span::after {
	transform: rotate(-50deg) translateZ(0);
	transform-origin: 0 16px;
	margin: -1px 0 0 -8px;
}

.subs-form .form-success .subs-success {
	opacity: 1;
	visibility: visible;
	transform: scale(1, 1) translateZ(0);
}

.subs-form .form-success .subs-success span::before {
	width: 8px;
	transition-delay: 0s;
	transition-timing-function: cubic-bezier(0.6, -0.28, 0.74, 0.05);
}

.subs-form .form-success .subs-success span::after {
	width: 16px;
	transition-delay: 0.12s;
}

.subs-form .subs-error {
	position: absolute;
	top: 100%;
	left: 0;
	width: 100%;
	height: 14px;
	font-size: 10px;
	line-height: 14px;
	color: red;
	max-width: 100%;
	white-space: nowrap;
	text-overflow: ellipsis;
	overflow: hidden;
	opacity: 0;
	z-index: -1;
	user-select: none;
	pointer-events: none;
	transition-property: opacity;
	transition-duration: 0.1s;
}

.subs-form .error ~ .subs-error {
	opacity: 1;
}

