@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

/* ms-drop */
.ms-drop__drop-js {
	opacity: 0;
	visibility: hidden;

	.is-open & {
		opacity: 1;
		visibility: visible; } }

/* ms-drop end */

/* user form */
.user-form {
	margin-bottom: $bottom-space;

	&:last-child {
		margin-bottom: 0; }

	.form-title {
		display: block;
		@include font-alt-bold;
		color: $title-color;
		text-align: center;
		margin-bottom: $bottom-space;

		// font size
		& {
			@include font-h2; } }

	form {
		display: block; }

	.input-wrap {
		&:last-child {
			margin-bottom: 0; } }

	textarea {
		height: 130px;
		max-height: 130px;
		resize: none; }

	.form-buttons {
		float: left;
		margin-right: 30px;

		@include media('<mob-lg') {
			float: none;
			margin: 0; }

		[class*="btn-"] {
			@include media('<mob-lg') {
				display: block;
				width: 100%;
				margin-top: $bottom-space--row;

				&:first-child {
					margin-top: 0; } } } }

	.form-note {
		overflow: hidden;
		min-height: $btn-height;
		@include clear-spacing;
		line-height: 25px;

		&::before {
			content: '';
			display: inline-block;
			vertical-align: middle;
			min-height: inherit; } }

	.form-note__item {
		display: inline-block;
		vertical-align: middle;
		width: calc(100% - 6px);
		@include font($font-size--small, $line-height--small);
		letter-spacing: $letter-spacing; } }

/* user form end */

/* slider */
$slider-button-size: 46px 46px; // h w
$slider-button-size--md: 46px 46px; // h w
$slider-arrow-size: 34px;
$slider-arrow-size--md: 24px;

// slider arrows
.slider-arrows {
	height: 0;
	position: absolute;
	width: 100%;
	left: 0;
	bottom: 100%;
	margin-bottom: $bottom-space--article;
	pointer-events: none; }

.slider-arrows__holder {
	position: relative; }

.slider-arrow {
	position: absolute;
	top: 50%;
	@include size(nth($slider-button-size, 1), nth($slider-button-size, 2));
	margin-top: -#{round(nth($slider-button-size, 2)/2)};
	color: $base-color;
	will-change: transform;
	//background: rgba(91, 255, 176, 0.51)
	// для тестирования, чтобы видеть контуры кнопки
	//background: none
	z-index: 5;
	cursor: pointer;
	user-select: none;
	-webkit-tap-highlight-color: transparent;

	.slider-arrows & {
		left: auto;
		top: auto;
		bottom: 0;
		pointer-events: auto;
		margin: round((50 - nth($slider-button-size, 1))/2) 0; }

	@include media('<tab-lg') {
		@include size(nth($slider-button-size--md, 1), nth($slider-button-size--md, 2));
		margin-top: -#{round(nth($slider-button-size--md, 2)/2)}; }

	svg {
		display: block;
		position: absolute;
		left: 50%;
		top: 50%;
		width: $slider-arrow-size;
		height: $slider-arrow-size;
		margin: -#{round($slider-arrow-size/2)} 0 0 -#{round($slider-arrow-size/2)};
		// transition
		$prop: opacity;
		will-change: $prop;
		transition: {
			property: $prop;
			duration: $animation-speed; }

		& + svg {
			opacity: 0; } }

	path {
		fill: currentColor; } }

// prev arrow
.slider-arrow_prev {
	left: 0;

	.slider-arrows & {
		right: nth($slider-button-size, 2); } }

// next arrow
.slider-arrow_next {
	right: 0;

	.slider-arrows & {
		right: 0; } }

// hover, disabled, active
.slider-arrow {

	// hover
	&:not(.swiper-button-disabled):hover {
		svg {
			& + svg {
				opacity: 1; } } }

	// active
	&:not(.swiper-button-disabled):active {
		svg {
			transform: translate(0, 1px); } }

	// disabled
	&.swiper-button-disabled {
		pointer-events: auto;
		cursor: default;
		opacity: 0.33; } }


/* slider pagination */
$slider-pag-size: 10px 10px; // h w

.swiper-pagination-bullet {
	display: inline-block;
	vertical-align: top;
	@include size(#{nth($slider-pag-size, 2)}, #{nth($slider-pag-size, 1)});
	border-radius: 0;
	border: 1px solid currentColor;
	color: $base-color;
	opacity: 1;
	transform: skew($skew, 0);
	margin: 4px;

	background: none; }

.swiper-pagination-bullet-active {
	background-color: currentColor; }

/* slider end */

/* tape-slider */
.tape-slider {
	position: relative;
	margin-bottom: $bottom-space--article;

	&:last-child {
		margin-bottom: 0; }

	.swiper-container {
		border: 1px solid $border-color;
		margin-left: -2px;
		margin-right: -1px; }

	.swiper-slide {
		border: 0 solid $border-color;
		border-left-width: 1px; }

	.swiper-pagination {
		margin-top: $bottom-space--article;
		position: relative;
		line-height: 0; } }

/* tape-slider end */

