/* product labels */
.i {
	svg {
		$size: 20px;
		display: block;
		position: absolute;
		left: 50%;
		top: 50%;
		width: $size;
		height: $size;
		margin: -#{round($size/2)} 0 0 -#{round($size/2)};

		path {
			fill: currentColor; } } }

.prod__labels {
	position: relative; }

.prod__label {
	$size: 34px;

	display: block;
	width: $size;
	height: $size;
	border-radius: round($size/2);
	color: white;
	text-indent: -9999px;
	overflow: hidden;
	background-color: $base-color;
	background-repeat: no-repeat;
	background-position: center;
	background-size: 16px auto;

	& ~ .prod__label {
		margin-top: 10px; } }

.prod__label--new {
	background-image: url(../../img/icon-new.svg);
	background-size: 20px auto; }

.prod__label--hit {
	background-image: url(../../img/icon-like.svg); }

.prod__label--share {
	background-image: url(../../img/icon-act.svg); }

.prod__label--sale {
	background-image: url(../../img/icon-sale.svg);
	background-size: 14px auto; }

/* product labels end */

/* product item */
.prod {
	position: relative;
	display: block;
	height: 100%;
	text-decoration: none;
	overflow: hidden;
	color: inherit; }

a.prod__inner {
	display: block;
	height: 100%;
	padding: nth($products-grid-space, 1) nth($products-grid-space, 2) 80px;
	text-decoration: none;
	color: inherit; }

.prod__labels {
	position: absolute;
	left: nth($products-grid-space, 2);
	top: nth($products-grid-space, 2);
	z-index: 1; }

.prod__figure {
	position: relative;
	max-width: 240px; // ограничиваем ширину изображения продукта
	padding: 0;
	margin: 0 auto $bottom-space--element;

	@include media('<mob-lg') {
		margin-top: 40px; } // a place for labels

	@include media('<mob-md') {
		max-width: 160px; }

	&:last-child {
		margin-bottom: 0; }

	&:before {
		content: "";
		display: block;
		@include ratio(1, 1); }

	img {
		display: block;
		position: absolute;
		left: 0;
		top: 0;
		width: 100%;
		height: 100%;
		max-width: none;
		@include object-fit(scale-down); } }

.prod__code, .prod__title {
	display: block;
	@include font-normal;
	@include font(18px, round-float(24/18, 2));
	letter-spacing: 0.1em;
	text-align: center;
	max-width: 260px;
	margin: 0 auto; }

.prod__code {
	color: $text-color-light; }

.prod__title {
	margin-bottom: 0.5em;

	&:last-child {
		margin-bottom: 0; } }

// info (show hover hover)
.prod__info {
	position: absolute;
	left: 0;
	top: 0;
	width: 100%;
	height: 100%;
	z-index: 5;
	pointer-events: none; }

.prod__body {
	position: absolute;
	width: 100%;
	left: 50%;
	top: 50%;
	padding: nth($products-grid-space, 1) nth($products-grid-space, 2) 180px;
	transform: translate(-50%, -50%); }

// list of characteristics
.prod__dl-list {
	margin-bottom: $bottom-space--element;
	font-size: 1.8rem;
	line-height: round-float(22/18, 2);
	text-align: center;

	&:last-child {
		margin-bottom: 0; } }

.prod__dl-list__di {
	position: relative;
	margin-top: 14px; }

.prod__dl-list__dt {
	display: block;
	color: $text-color-light; }

// footer
.prod__foot {
	position: absolute;
	left: 0;
	bottom: 0;
	width: 100%;
	padding: 0 nth($products-grid-space, 2)  nth($products-grid-space, 1);
	z-index: 5; }

// price
.prod__price {
	text-align: center;
	line-height: 24px; }

.prod__price__cur,
.prod__price__old {
	position: relative;
	display: inline-block;
	@include margin-horizontal(7px); }

.prod__price__cur__val {
	@include font-black;
	font-size: 24px; }

.prod__price__old {
	color: $base-color;
	text-decoration: line-through;

	&::before {
		content: "/ ";
		color: $text-color;
		display: block;
		position: absolute;
		right: 100%;
		top: 0;
		margin-right: 5px; } }

.prod__price__old__val {
	@include font-normal; }

// add info
.prod__add-info {
	position: absolute;
	left: 0;
	bottom: 0;
	width: 100%;
	padding: 0 nth($products-grid-space, 2)  nth($products-grid-space, 1);
	z-index: 5; }

// hover animation
.prod {
	$animation-speed: 0.53s;
	$animation-elements: ".prod__labels, .prod__figure, .prod__dl-list__di, .prod__code, .prod__title, .prod__foot, .prod__add-info";

	#{$animation-elements} {
		// transition
		$prop: transform, opacity;
		will-change: $prop;
		transition: {
			property: $prop;
			duration: $animation-speed; } }

	.prod__dl-list__di {
		opacity: 0;
		$x: -50px;
		$y: 0;
		transform: translate($x, $y);
		transform: translate3d($x, $y, 0);

		@for $i from 1 through 4 {
			&:nth-child(#{$i + 1}) {
				transition: {
					delay: 0.05s * $i; } } } }

	.prod__foot {
		pointer-events: none; }

	.prod__add-info {
		opacity: 0;
		pointer-events: none;
		$x: 0;
		$y: 50px;
		transform: translate($x, $y);
		transform: translate3d($x, $y, 0); }

	// hover
	.no-touchevents &:hover {
		$animation-speed: 0.35s;
		#{$animation-elements} {
			transition-duration: $animation-speed; }

		.prod__labels {
			opacity: 0; }

		.prod__figure {
			opacity: 0.1;
			$scale: 0.88;
			transform: scale($scale);
			transform: scale3d($scale, $scale, 1); }

		.prod__info {
			pointer-events: auto; }

		.prod__dl-list__di {
			opacity: 1;
			$x: 0;
			$y: 0;
			transform: translate($x, $y);
			transform: translate3d($x, $y, 0); }

		.prod__code,
		.prod__title {
			opacity: 0;
			$x: 0;
			$y: -50px;
			transform: translate($x, $y);
			transform: translate3d($x, $y, 0); }

		.prod__foot {
			$x: 0;
			$y: -110px;
			transform: translate($x, $y);
			transform: translate3d($x, $y, 0); }

		.prod__add-info {
			opacity: 1;
			$x: 0;
			$y: -20px;
			transform: translate($x, $y);
			transform: translate3d($x, $y, 0);

			[class*="btn-"] {
				pointer-events: auto; } } } }

/* product item end */

/* products list */
.main-section--products {
	overflow: hidden; }

.products {
	overflow: hidden;
	margin-bottom: $bottom-space--article;

	.main-section--products & {
		margin-left: -2px;
		margin-right: -2px; }

	&:last-child {
		margin-bottom: 0; } }

.products__list {
	display: flex;
	flex: 0 1 auto;
	justify-content: stretch;
	flex-flow: row wrap;
	@include clear-spacing; }

.products__item {
	@include grid-item(5, nth($products-space, 2), nth($products-space, 1));
	@include font($font-size, $line-height);
	letter-spacing: $letter-spacing;

	// Оставить 10 елементов
	// Остальные скрыть
	.main-section--products & {
		&:nth-child(n + 11) {
			display: none; } }

	@include media('<desk-sm') {
		@include grid-item-responsive(3, nth($products-space, 2), nth($products-space, 1)); }

	@include media('<mob-lg') {
		@include grid-item-responsive(2, nth($products-space, 2), nth($products-space, 1)); } }

// дополнительные бордеры нужны для safari
// т.к. в этом браузере не поддерживается автовысота во flex-box элементах
// подходит только, если не нужно делать всю область кликабельной
.products, .products__list, .products__item {
	position: relative;

	&::before, &::after {
		content: "";
		display: block;
		position: absolute;
		border: 0 solid $border-color; } }

.products {
	padding-right: 1px;

	&::before {
		top: 0;
		left: 0;
		@include size(0, 9999px);
		border-left-width: 1px; }

	&::after {
		left: 0;
		bottom: 0;
		@include size(9999px, 0);
		border-bottom-width: 1px; } }

.products__list, .products__item {
	&::before {
		top: 0;
		left: 0;
		@include size(9999px, 0);
		border-bottom-width: 1px; }

	&::after {
		right: -1px;
		top: 0;
		@include size(0, 9999px);
		border-right-width: 1px; } }

// дополнительные бордеры нужны для safari END
/* products list end */

/* main sections */
.main-section {
	padding-top: nth($main-section-space-vertical, 1);
	padding-bottom: nth($main-section-space-vertical, 2); }

.main-section__heading {
	position: relative;
	margin-bottom: $bottom-space--article;

	h1, h2, h3 {
		@include font-normal;
		font-size: 4.8rem;
		line-height: round-float(52/48, 2);
		color: $text-color;

		& > span {
			display: inline-block;
			vertical-align: top;
			position: relative;

			&::after {
				content: "";
				display: block;
				position: absolute;
				left: 100%;
				bottom: 10px;
				width: 100px;
				height: 2px;
				background-color: currentColor;
				margin-left: 20px; } } } }

/* main sections end */

/* main enter */
.main-enter {
	position: relative;
	overflow: hidden;
	margin-bottom: $bottom-space--article;
	background-color: rgba($img-mask-bg, 0.5);

	&:last-child {
		margin-bottom: 0; } }

/* main enter end */

/* promo slider */
.main-section--promo {
	height: calc(100vh - #{$header-height} - #{$nav-height});
	min-height: 700px;
	padding-top: 0;
	padding-bottom: 0; }

.promo-slider {
	$slider-button-size: 60px 64px;
	//width: 600px
	height: 100%;
	//background-color: $bg-color-alt
	color: white;

	@include media('<mob-lg') {
		color: $text-color;
		padding-bottom: 60px; }

	// arrows, pagination
	.slider-arrow {
		top: calc((100% - 25%) / 2);
		color: white;
		@include size(nth($slider-button-size, 1), nth($slider-button-size, 2));
		margin-top: -#{round(nth($slider-button-size, 2)/2)};

		&::before, &::after {
			content: "";
			display: block;
			position: absolute;
			width: 100%;
			height: 100%;
			left: 0;
			top: 0;
			background-color: $base-color;
			z-index: -1; }

		&::after {
			transform: skew($skew, 0); } }

	.slider-arrow_prev {
		&::after {
			transform-origin: bottom right; } }

	.slider-arrow_next {
		&::after {
			transform-origin: top left; } }

	.swiper-pagination {
		line-height: 0;
		bottom: 25%;
		margin-bottom: $bottom-space--article; }

	.swiper-pagination-bullet {
		vertical-align: top;
		color: white; } }

.promo-slider__item {
	display: flex;
	flex-direction: column;
	text-align: center;
	justify-content: center;
	align-items: center;
	position: relative;
	//padding-bottom: $parts-height
	//padding-bottom: 25%
	overflow: hidden;

	@include image-mask;

	&::after {
		position: relative;
		content: "";
		display: block;
		width: 100%;
		height: 25%;
		pointer-events: none;
		z-index: -1; } }

.promo-slider__item__img {
	position: absolute;
	@include size(100%);
	@include position(0);
	background-color: $bg-color-alt;
	z-index: -2;

	@include media('<mob-lg') {
		position: relative;
		@include size(auto);
		@include position(auto);
		z-index: auto; }

	&::before {
		content: none;
		display: block;
		@include ratio(172, 85);

		@include media('<mob-lg') {
			content: ""; } }

	img {
		display: block;
		@include size(100%);
		max-width: none;
		@include object-fit(cover);

		@include media('<mob-lg') {
			position: absolute;
			left: 0;
			top: 0; } } }

.promo-slider__item__content {
	//+display-inline-block-top
	width: 960px;
	text-align: center; }

.promo-slider__item__title {
	$font-size: 60px;
	$font-size--hd: 60px;
	$font-size--lg: 48px;
	$font-size--md: 44px;
	$font-size--sm: 30px;
	$font-size--mob: 26px;
	$line-height: round-float(72/60, 1);
	$line-height--sm: round-float(36/30, 1);
	// ===============
	display: block;
	overflow: hidden;
	max-width: 100%;
	max-height: round($font-size*$line-height*3); // maximum 5 line
	margin-bottom: 5rem;
	@include font-black;
	@include font($font-size, $line-height);
	letter-spacing: 0.05em;
	text-align: center;
	cursor: default;

	@include media('<desk-sm') {
		width: 600px;
		font-size: $font-size--md;
		max-height: round($font-size--md*$line-height*5); } // maximum 5 line

	@include media('<mob-lg') {
		width: auto;
		font-size: $font-size--sm;
		line-height: $line-height--sm;
		max-height: round($font-size--sm*$line-height--sm*5); // maximum 5 line
		margin-bottom: 2rem; }

	@include media('<mob-md') {
		font-size: $font-size--mob;
		max-height: round($font-size--mob*$line-height--sm*5); } // maximum 5 line

	&:last-child {
		margin-bottom: 0; } }

.promo-slider__item__footer {
	.more-link {
		@include display-inline-block-top;
		text-decoration: none;
		color: inherit;

		span {
			@include display-inline-block-top;
			line-height: 1.5;
			border-bottom: 2px solid; }

		&:hover {
			color: $base-color;

			span {
				border-color: $base-color; } } } }

.slider-progress {
	display: block;
	position: absolute;
	left: 0;
	bottom: 25%;
	height: 3px;
	width: 100%;
	background-color: rgba(white, 0.25);
	z-index: 5; }

.slider-progress__load {
	position: absolute;
	left: -100%;
	top: 0;
	width: 100%;
	height: 100%;
	background-color: $base-color;
	transform: translate(38%, 0); }

/* promo slider end */

/* parts */
$width: 25%;

.main-section--parts {
	position: relative;
	pointer-events: none;
	padding-top: 0;
	padding-bottom: 0;

	.main-enter & {
		display: flex;
		flex-direction: column;
		justify-content: flex-end;
		position: absolute;
		width: 100%;
		height: 100%;
		left: 0;
		bottom: 0;
		z-index: 5; } }

.parts {
	height: 25%;
	pointer-events: auto; }

.parts__list {
	height: 100%; }

.parts__item {
	height: 100%;
	float: left;
	width: 25%; }

.parts__item__img {
	position: absolute;
	left: 0;
	top: 0;
	width: 100%;
	height: 100%;
	z-index: -1;
	opacity: 0;
	pointer-events: none;
	//$y: 20rem
	//$x: 20rem
	//transform: translate(0, $y)
	//transform: translateY($y)
	// transition
	$prop: transform, opacity, visibility;
	will-change: $prop;
	transition: {
		property: $prop;
		duration: $parts-animation-speed-out; }

	@include image-mask;

	img {
		position: relative;
		width: 100%;
		height: 100%;
		max-width: none;
		@include object-fit(cover);
		z-index: -2; } }

.parts__item__thumb {
	position: relative;
	overflow: hidden;
	height: 100%;
	color: white;
	z-index: 10;

	&::before {
		content: "";
		display: block;
		position: absolute;
		left: 0;
		top: 0;
		width: 100%;
		height: 100%;
		z-index: -1;
		background-color: $base-color;
		opacity: 0;
		// transition
		$prop: opacity, transform;
		will-change: $prop;
		transition: {
			property: $prop;
			duration: $parts-animation-speed-out;
 } }			//delay: $parts-animation-speed

	.p1, .p2 {
		display: block;
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		position: absolute;
		left: 0;
		top: 0;
		height: 100%;
		padding: 0 10px;
		z-index: 1;
		@include font-bold;
		color: inherit;
		text-align: center;
		text-decoration: none;
		border: 0 solid rgba(white, 0.25);
		// transition
		$prop: opacity;
		will-change: $prop;
		transition: {
			property: $prop;
			duration: $parts-animation-speed-out; }

		.t, .l {
			width: 100%;
			// transition
			$prop: transform;
			will-change: $prop;
			transition: {
				property: $prop;
				duration: $parts-animation-speed-out; } }

		.t {
			font-size: 3rem;
			line-height: round-float(30/30, 2);
			letter-spacing: 0.05em;
			margin-bottom: 0.3em; }

		.l {
			opacity: 0.5;
			font-size: 1.8rem;
			line-height: round-float(30/18, 2);
			letter-spacing: 0.05em; }

		svg {
			$width: 42px;
			$height: 20px;
			display: block;
			position: absolute;
			bottom: 20px;
			left: 50%;
			height: $height;
			width: $width;
			margin-left: -#{round-float($width/2, 2)};
			opacity: 0;
			// transition
			$prop: transform, opacity;
			will-change: $prop;
			transition: {
				property: $prop;
				duration: $parts-animation-speed; }

			path {
				fill: currentColor; } } }

	.p1 {
		width: 50%;
		left: 0;

		.t, .l {
			$x: 50%;
			transform: translate($x, 0);
			transform: translateX($x); } }

	.p2 {
		width: 50%;
		left: 50%;
		border-left-width: 1px;
		opacity: 0;
		pointer-events: none;

		&::before {
			$size: 120px;
			content: "";
			display: block;
			position: absolute;
			width: $size;
			height: $size;
			background: url(../../img/icon-new.svg) no-repeat;
			background-size: contain;
			opacity: 0.2;
			$x: 50%;
			transform: translate($x, 0);
			transform: translateX($x);
			// transition
			$prop: transform;
			will-change: $prop;
			transition: {
				property: $prop;
				duration: $parts-animation-speed-out; } }

		.t, .l {
			$x: 20%;
			transform: translate($x, 0);
			transform: translateX($x); } }

	.p1, .p2 {
		&:hover {
			//background-color: #343434

			svg {
				opacity: 1; } } } }

.parts__item__prods {
	$space: 30px;
	position: absolute;
	overflow: hidden;
	left: 0;
	bottom: 25%;
	height: 75%;
	z-index: 1;
	width: $width;
	pointer-events: none;

	& > ul {
		display: flex;
		flex-direction: column;
		list-style: none;
		margin: 0;
		padding: 0;
		height: 100%;
		background-color: rgba(white, 1);
		opacity: 0;
		$y: 10rem;
		transform: translate(0, $y);
		transform: translateY($y);
		// transition
		$prop: transform, opacity, visibility;
		will-change: $prop;
		transition: {
			property: $prop;
			duration: $parts-animation-speed-out; }

		&::before {
			$color: black;
			content: "";
			display: block;
			position: absolute;
			left: 0;
			top: 0;
			width: 100%;
			height: 26px;
			@include linear-gradient(to bottom, rgba($color, 0.1) 0%, rgba($color, 0) 100%);
			opacity: 0;
			pointer-events: none;
			z-index: 5;
			// transition
			$prop: opacity;
			will-change: $prop;
			transition: {
				property: $prop;
				duration: $parts-animation-speed-out; } }

		& > li {
			position: relative;
			flex-grow: 1;
			flex-shrink: 1;
			height: 33.33%;
			max-height: 33.33%;

			&::before {
				content: "";
				display: block;
				position: absolute;
				top: 0;
				left: $space;
				right: $space;
				border-top: 1px solid #e6e6e6; }

			&:first-child {
				&::before {
					content: none; } }

			& > a {
				display: block;
				position: relative;
				height: 100%;
				padding: $space;
				color: inherit;
				text-decoration: none; }

			.prod__labels {
				position: absolute;
				left: $space;
				top: $space; }

			img {
				float: left;
				width: 34%;
				max-width: 144px;
				margin-right: 30px; } } }

	ul ul {
		list-style: none;
		margin: 0;
		padding: 20px 0 0 0;
		overflow: hidden;
		@include font-normal;
		font-size: 1.8rem;
		line-height: 2.4rem;
		color: #000;

		& > li {
			position: relative;
			@include truncate(100%);

			&:first-child {
				@include font-normal;
				color: #b2b2b2; }

			&:last-child {
				margin-top: 15px;
				b {
					@include font-black;
					font-size: 2.4rem; } } } } }

.parts__item {
	&:nth-child(1) {
		.parts__item__prods {
			left: $width*0; } }

	&:nth-child(2) {
		.parts__item__prods {
			left: $width*1; } }

	&:nth-child(3) {
		.parts__item__prods {
			left: $width*2; } }

	&:nth-child(4) {
		.parts__item__prods {
			left: $width*3; } } }

// hover
.main-enter {
	.main-section--promo {
		// transition
		$prop: opacity;
		will-change: $prop;
		transition: {
			property: $prop;
			duration: $animation-speed; } }

	.no-touchevents &:hover {
		.main-section--promo {
			opacity: 0;

			&:hover {
				opacity: 1; } } } }

.parts__item:hover {
	.parts__item__img {
		opacity: 1;
		transition: {
			duration: $parts-animation-speed; } }

	.parts__item__thumb {
		&:before {
			opacity: 1;
			transition: {
				duration: $parts-animation-speed;
 } }				//delay: 0s

		.p1, .p2 {
			.t, .l {
				$x: 0;
				transform: translate($x, 0);
				transform: translateX($x);
				transition: {
					duration: $parts-animation-speed; } } }

		.p2 {
			opacity: 1;
			pointer-events: auto;

			&::before {
				$x: 0;
				transform: translate($x, 0);
				transform: translateX($x);
				transition: {
					duration: $parts-animation-speed; } } } }

	.parts__item__prods {
		pointer-events: auto;

		& > ul {
			opacity: 1;
			$y: 0;
			transform: translate(0, $y);
			transform: translateY($y);
			transition: {
				duration: $parts-animation-speed;
 }				//delay: $parts-animation-speed

			&::before {
				opacity: 1; } } } }

/* parts end */

/* collections */
.main-section--collections {
	overflow: hidden; }

.collection-slider__item {
	display: block;
	text-decoration: none;
	color: inherit;
	padding: $grid-space;
	@include font(1.8rem, round-float(30/18, 2));

	&:first-child {
		border-left-width: 0; } }

.collection-slider__item__inner {
	display: block;
	text-decoration: none;
	margin: 0 auto;
	width: 250px;
	max-width: 100%; }

.collection-slider__item__img {
	text-align: center;
	margin-bottom: $bottom-space--element;

	&:last-child {
		margin-bottom: 0; }

	img {
		margin: 0 auto; } }

.collection-slider__item__title {
	@include font-black;
	@include font(2.4rem, round-float(30/24, 2));
	color: inherit;
	text-align: center;
	margin-bottom: $bottom-space--element;

	&:last-child {
		margin-bottom: 0; } }

/* collections end */

/* news */
.main-section--news {
	overflow: hidden; }

.news-slider {
	margin: 0 auto $bottom-space--article;
	position: relative;

	&:last-child {
		margin-bottom: 0; }

	.swiper-container {
		overflow: visible; }

	.swiper-pagination {
		margin-top: $bottom-space--article;
		position: relative;
		line-height: 0; }

	// arrows, pagination
	.slider-arrows,
	.swiper-pagination {
		position: absolute;
		margin-bottom: 0;
		margin-top: $bottom-space--article;
		top: 100%; }

	.slider-arrows {
		bottom: auto;

		.slider-arrow {
			bottom: auto;
			top: 0; } }

	.swiper-pagination {
		display: flex;
		align-items: center;
		left: 0;
		height: 50px; } }

.news-slider__item {
	padding-right: nth($grid-space, 2);

	&:nth-child(even) {
		padding-top: 120px; } }

.news-slider__item__inner {
	display: block;
	position: relative;
	text-decoration: none;
	width: 240px;
	max-width: 100%;
	color: inherit; }

.news-slider__item__date {
	position: absolute;
	left: 0;
	bottom: 0;
	transform-origin: left bottom;
	transform: rotate(-90deg);
	color: #bfbfbf;
	padding-bottom: 1em; }

.news-slider__item__img {
	position: relative;
	margin-bottom: $bottom-space--element; }

.news-slider__item__title {
	@include font-bold;
	@include font(2.4rem, round-float(30/24, 2));
	// transition
	$prop: color;
	transition: {
		property: $prop;
		duration: $animation-speed; } }

// hover
.news-slider__item__inner {
	&:hover {
		.news-slider__item__title {
			color: $base-color; } } }

/* news end */

/* subscription form */
.subscribe {
	background-color: #282828;
	color: white;
	@include padding-vertical(4rem); }

.subs-form {
	$subs-form-size: 50px 100%; // h w
	$subs-form-border-radius: 0;

	text-align: left;

	.input-wrap {
		display: flex;
		align-items: center;
		margin: 0; }

	.label-holder {
		width: 50%;
		margin: 0 17px 0 0; }

	.label-holder, label {
		@include font-normal;
		@include font(1.8rem, round-float(22/18, 2));
		color: inherit; }

	label {
		b {
			display: block;
			@include font-normal;
			@include font(3.0rem, round-float(30/18, 2)); } }

	.form-fields {
		width: 50%;
		position: relative;

		@include media('<mob-lg') {
			width: 100%; } }

	.input-holder {
		position: relative;
		height: nth($subs-form-size, 1);
		padding-right: 165px;
		$prop: opacity;
		will-change: $prop;
		transition: {
			property: $prop;
			duration: 0.25s; }

		input {
			height: nth($subs-form-size, 1);
			font-size: 16px;
			line-height: nth($subs-form-size, 1);
			padding: 0 20px;
			border-radius: 0;

			&:-webkit-autofill {
				-webkit-box-shadow: 0 0 0 30px $form-bg-color inset; }

			&:-webkit-autofill {
				-webkit-text-fill-color: $text-color; } } }

	.form-success .input-holder {
		opacity: 0;
		visibility: hidden;
		$scale: 0.76;
		transform: scale($scale);
		transform: scale3d($scale, $scale, 1);
		pointer-events: none; }

	input[type='submit'], button {
		display: inline-block;
		vertical-align: top;
		height: nth($subs-form-size, 1);
		width: 165px;
		min-width: 0;
		padding: 0 10px;
		position: absolute;
		top: 0;
		right: 0;
		border: none;
		@include font-bold;
		font-size: 18px;
		line-height: nth($subs-form-size, 1);
		letter-spacing: 0.1em;
		text-align: center;
		color: white;
		cursor: pointer;
		box-shadow: none;
		background-color: $base-color;
		border-radius: 0; }

	.subs-success {
		position: absolute;
		@include position(0);
		@include size(100%);
		opacity: 0;
		pointer-events: none;
		background-color: $base-color;
		transform: scale(0.76, 0.76) translateZ(0);
		border-radius: $subs-form-border-radius;
		//text-indent: -9999px
		//overflow: hidden
		text-align: center;
		color: white;
		font-size: 16px;
		line-height: nth($subs-form-size, 1);

		transition: {
			property: opacity, visibility, transform;
			duration: 0.25s; }

		span {
			position: relative;
			display: inline-block;
			vertical-align: top;
			//position: absolute
			//+position(50%)
			//+size(24px, 16px)
			//margin: -8px 0 0 -12px

			&::before, &::after {
				@include pseudo();
				@include size(0, 2px);
				left: 0;
				top: 50%;
				background-color: currentColor;
				$prop: width;
				will-change: $prop;
				transition: {
					property: $prop;
					duration: 0.12s;
					timing-function: linear; } }

			&::before {
				margin: -3px 0 0 -29px;
				transform: rotate(45deg) translateZ(0);
				transform-origin: 0 8px;
				transition-delay: 0.12s; }

			&::after {
				transform: rotate(-50deg) translateZ(0);
				transform-origin: 0 16px;
				margin: -1px 0 0 -8px; } } }

	.form-success .subs-success {
		opacity: 1;
		visibility: visible;
		transform: scale(1, 1) translateZ(0);

		span {
			&::before {
				width: 8px;
				transition: {
					delay: 0s;
					timing-function: cubic-bezier(0.6, -0.28, 0.74, 0.05); } }

			&::after {
				width: 16px;
				transition-delay: 0.12s; } } }

	.subs-error {
		position: absolute;
		top: 100%;
		left: 0;
		@include size(100%, 14px);
		@include font(10px, 14px);
		color: $form-error-color;
		@include truncate(100%);
		opacity: 0;
		z-index: -1;
		user-select: none;
		pointer-events: none;

		transition: {
			property: opacity;
			duration: 0.1s; } }

	.error ~ .subs-error {
		opacity: 1; } }

/* subscription form end */
