@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

html {
	font-size: 62.5%;

	// блокировка скролла
	&.css-scroll-fixed:not(.open-only-mob) {
		@include scroll-blocked; }

	&.css-scroll-fixed.open-only-mob {
		@include media('<tab-lg') {
			@include scroll-blocked; } } }

/* === LAYOUT === */
.max-wrap {
	max-width: $page-layout-width; }

.layout {
	@include padding-horizontal(nth($page-layout-space, 2));
	@include push-auto;

	@include media('<mob-lg') {
		@include padding-horizontal(nth($page-layout-space--mob, 2)); } }

.layout-space {
	@include padding-horizontal(nth($page-layout-space, 2));

	@include media('<mob-lg') {
		@include padding-horizontal(nth($page-layout-space--mob, 2)); } }

// выравнивание текстовых элементов на странице статьи (контент обернут в .layout-article)
// и их максимальная ширина
.layout-article {
	padding: $text-layout-space;

	@include media('<tab-lg') {
		padding: $text-layout-space--md; }

	@include media('<tab-sm') {
		padding: $text-layout-space--sm; }

	& > p, & > ul, & > ol, & > h1, & > h2, & > h3, & > h4, & > h5, & > h6, & > hr, & > blockquote, & > .layout-text, .imgc figcaption {
		max-width: $text-layout-width;
		@include padding-horizontal(0);
		@include push-auto; }

	.layout-flood {
		max-width: $flood-layout-width;
		@include padding-horizontal(0);
		@include push-auto; }

	// элеметы без отступов по бокам
	// минусовые маржины по горизонтале имеют значение только внутри .layout-article
	.layout-full-width, .layout-wide {

		@include margin-horizontal(-#{nth($text-layout-space, 2)});

		@include media('<tab-lg') {
			@include margin-horizontal(-#{nth($text-layout-space--md, 2)}); }

		@include media('<tab-sm') {
			@include margin-horizontal(-#{nth($text-layout-space--sm, 2)}); } } }

// максимальная ширина текстовых элементов на внутренней (текстовой) странице
.max-wrap-text {
	//max-width: #{$text-layout-width + (nth($text-layout-space, 2))*2}
	max-width: $text-layout-width;

	//+media('<tab-lg')
	//ax-width: #{$text-layout-width + (nth($text-layout-space--md, 2))*2}

	//+media('<tab-sm')
 }	//ax-width: #{$text-layout-width + (nth($text-layout-space--sm, 2))*2}

// выравнивание текстовых элементов на внутренней (текстовой) странице
// и их максимальная ширина
.layout-text {
	max-width: #{$text-layout-width + (nth($text-layout-space, 2))*2};
	@include padding-horizontal(nth($text-layout-space, 2));
	@include push-auto;

	@include media('<tab-lg') {
		max-width: #{$text-layout-width + (nth($text-layout-space--md, 2))*2};
		@include padding-horizontal(nth($text-layout-space--md, 2)); }

	@include media('<tab-sm') {
		max-width: #{$text-layout-width + (nth($text-layout-space--sm, 2))*2};
		@include padding-horizontal(nth($text-layout-space--sm, 2)); } }

// максимальная ширина альтернативных элементов на внутренней (текстовой) странице
.max-wrap-flood {
	//max-width: #{$flood-layout-width + (nth($text-layout-space, 2))*2}
	max-width: $flood-layout-width;

	//+media('<tab-lg')
	//ax-width: #{$flood-layout-width + (nth($text-layout-space--md, 2))*2}

	//+media('<tab-sm')
 }	//ax-width: #{$flood-layout-width + (nth($text-layout-space--mob, 2))*2}

// выравнивание альтернативных элементов на внутренней (текстовой) странице
// и их максимальная ширина
.layout-flood {
	max-width: #{$flood-layout-width + (nth($page-layout-space, 2))*2};
	@include padding-horizontal((nth($page-layout-space, 2)));
	@include push-auto;

	@include media('<tab-lg') {
		max-width: #{$flood-layout-width + (nth($page-layout-space--mob, 2))*2};
		@include padding-horizontal((nth($page-layout-space--mob, 2))); }

	p, ul, ol, h1, h2, h3, h4, h5, h6, hr {
		max-width: none; } }

// элеметы с отступами по бокам
.layout-wide {
	@include padding-horizontal((nth($text-layout-space, 2)));

	@include media('<tab-lg') {
		@include padding-horizontal((nth($text-layout-space--md, 2))); }

	@include media('<tab-sm') {
		@include padding-horizontal((nth($text-layout-space--sm, 2))); }

	p, ul, ol, h1, h2, h3, h4, h5, h6, hr {
		max-width: none; } }

// элеметы без отступов по бокам
.layout-full-width {

	p, ul, ol, h1, h2, h3, h4, h5, h6, hr {
		max-width: none; } }

// выравнивание по центру блочных элементов
.push-auto {
	margin-left: auto;
	margin-right: auto; }

// layout страницы
.wrapper {
	position: relative;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	align-items: stretch;
	margin: 0 auto;
	min-height: 100%;
	max-width: 100%;
	width: $page-layout-width;
	background-color: $bg-color-alt;
	box-shadow: 0 0 0 1px #cdcdcd; }

/* === LAYOUT end === */

/* === CONTENT === */
.main {
	flex-shrink: 1;
	flex-grow: 1;
	//padding-top: $header-height

	@include media('<tab-lg') {
		padding-top: $header-height--sm; } }

/* === CONTENT end === */

/* === HEADER === */
.header {
	position: relative;
	height: $header-height;
	z-index: $z-i-header;

	@include media('<tab-lg') {
		//position: fixed
		//left: 0
		//top: 0
		//width: 100%
		min-width: $page-layout-min;
		height: $header-height--sm; } }

.header__holder {
	position: relative;
	height: 100%;
	//padding: 0
	background-color: $header-bg;
	//box-shadow: $header-shadow
	@include clear; }

/* logo */
.logo {
	display: block;
	position: absolute;
	left: 50%;
	top: 50%;
	width: nth($logo-size, 1);
	transform: translate(-50%, -50%);

	@include media('<mob-lg') {
		left: nth($page-layout-space--mob, 2);
		width: nth($logo-size--mob, 1); }

	&::before {
		content: "";
		display: block;
		@include ratio(nth($logo-size, 1), nth($logo-size, 2));
		background-repeat: no-repeat;
		background-position: left;
		background-color: $header-bg;
		background-image: url(../../img/logo.png);
		background-size: contain; }

	a, & > span {
		position: absolute;
		display: block;
		left: 0;
		top: 0;
		@include size(100%);
		z-index: 5;

		& > em {
			@include hide-text; } } }

/* logo end */
/* === HEADER end === */

/* === NAVIGATION === */
// variables
$nav-is-open-class: 'nav-is-open';

// nav layout
.nav {
	//position: fixed
	//left: 0
	//top: 0
	//width: 100%
	//min-width: $page-layout-min
	border-top: 1px solid $border-color;
	text-align: center;
	z-index: $z-i-nav;
	$prop: transform, opacity;
	will-change: $prop;
	transition: {
		property: transform, opacity;
		duration: $animation-speed; }

	@include media('<tab-lg') {
		position: fixed;
		width: $nav-width;
		height: calc(100% - #{$header-height--sm});
		left: 0;
		top: $header-height--sm;
		background-color: white;
		z-index: 9999;
		$x: -100%;
		transform: translate($x, 0); // for old browser
		transform: translateX($x);

		&.#{$nav-is-open-class} { // add js
			$x: 0;
			transform: translate($x, 0); // for old browser
			transform: translateX($x); } }

	@include media('<mob-lg') {
		width: 100%; } }

.nav__layout {
	display: inline-block;
	vertical-align: top;
	text-align: left;
	position: relative;
	height: 100%;

	@include media('<tab-lg') {
		padding: 0; } }

/* nav list */
// общие стили для пунктов навигации
.nav__list {
	//position: absolute
	//top: 0
	//left: #{nth($logo-size, 1) + nth($page-layout-space, 2) + 50}
	//height: $header-height

	@include media('<tab-lg') {
		position: relative;
		top: auto;
		left: auto;
		height: 100%;
		overflow-x: hidden;
		overflow-y: auto; }

	// убрать подсветку при клике на тачскринах
	.touchevents & {
		user-select: none;
		-webkit-tap-highlight-color: transparent; }

	li {
		position: relative; }

	.nav__tab {
		position: relative; }

	a {
		display: block;
		position: relative;
		padding: 8px 24px;
		color: $text-color;
		text-decoration: none;
		user-select: none;
		$prop: color, background-color, border-color;
		will-change: $prop;
		transition: {
			property: $prop;
			duration: $animation-speed; }

		@include media('<tab-lg') {
			@include padding-horizontal(nth($page-layout-space, 2)); }

		//+media('<mob-lg')
		//include padding-horizontal(nth($page-layout-space--mob, 2))

		// убрать подсветку при клике на тачскринах
		.touchevents & {
			-webkit-tap-highlight-color: transparent; }

		span {
			@include font(1.6rem, round-float(24/16, 2)); } }

	&, & ul {
		list-style: none;
		padding: 0;
		margin: 0; } }

// общие стили для подменю навигации
.nav__drop {
	// display: none
	// transition
	$prop: opacity, visibility, transform;
	will-change: $prop;
	transition: {
		property: $prop;
		duration: $animation-speed;
		timing-function: ease-in-out; }

	@include media('>=tab-lg') {
		position: absolute;
		left: 0;
		top: 100%;
		width: 280px;
		padding: 14px 0;
		border: 1px solid $border-color;
		background-color: #fff;
		opacity: 0;
		pointer-events: none;
		//visibility: hidden
		$transform: -10px, 0;
		transform: translate($transform);
		transform: translate3d($transform, 0); }

	.hover > & {
		@include media('>=tab-lg') {
			display: block;
			opacity: 1;
			pointer-events: auto;
			//visibility: visible
			$transform: 0, 0;
			transform: translate($transform);
			transform: translate3d($transform, 0); } }

	.no-js & {
		display: block;
		opacity: 1;
		visibility: visible; } }

// стили для пунктов навигации, у которых ЕСТЬ ПОДМЕНЮ
.nav__list li.has-drop > .nav__tab > a {
	@include media('<tab-lg') {
		padding-right: (round($nav-arrow-size+10)); } }

// стили для пункта навигации ТЕКУЩЕЙ страницы
.nav__list li.current > .nav__tab {

	& {
		a {
			color: $base-color;
			@include font-alt-bold; } } }

// стили для пункта навигации С ОТКРЫТЫМ ПОДМЕНЮ
//.nav__list li
//.is-open
//& > .nav__tab a
//	&::before
//		opacity: 1
//		transform: translate(0, 0)

//& > .nav__drop
//isplay: block

// нужно добавлять на nav__drop style="display: block"

// особые стили для пунктов навигации ПЕРВОГО урованя
.nav__list > li {
	float: left;
	height: $nav-height;

	@include media('<tab-lg') {
		float: none;
		height: auto;
		width: $nav-width;

		.touchevents & {
			width: auto; } }

	& > .nav__tab {
		height: 100%;
		//padding: 10px 0

		@include media('<tab-lg') {
			padding: 0;
			height: auto; } }

	& > .nav__tab a,
	& > a {
		display: flex;
		align-items: center;
		height: 100%;
		@include padding-vertical(5px);
		@include font-alt-bold;
		color: $text-color;
		border: 0 solid transparent;
		border-bottom-width: 2px;

		@include media('<tab-lg') {
			border: none;
			@include padding-vertical(15px); }

		span {
			@include font(2.0rem, round-float(24/20, 2)); } }

	// hover
	&.hover {
		// только для десктопа
		@include media('>=tab-lg') {
			& > .nav__tab a,
			& > a {
				border-color: $base-color;
				color: $base-color; } } } }

// СТРЕЛКИ навигации
.nav__angle {
	position: absolute;
	right: 0;
	top: 0;
	width: $nav-arrow-size;
	height: 100%;
	//border: solid #666
	//border-width: 0 0 0 1px
	transition-property: transform, color, background-color;
	transition-duration: $animation-speed;
	z-index: 2;
	cursor: pointer;
	display: none;

	// убрать подсветку при клике на тачскринах
	.touchevents & {
		user-select: none;
		-webkit-tap-highlight-color: transparent; }

	li.has-drop > .nav__tab > & {
		@include media('<tab-lg') {
			display: block; } }

	i {
		position: absolute;
		@include position(50%, 50%);
		@include size(20px);
		margin: -10px 0 0 -10px;
		background-color: $base-color;
		border-radius: 10px;
		pointer-events: none;
		transition-property: transform;
		transition-duration: $animation-speed;
		@include font(0, 0);

		&::before, &::after {
			@include pseudo();
			@include position(50%, 50%);
			height: 2px;
			width: 10px;
			margin: -1px -5px;
			border-radius: 2px;
			backface-visibility: hidden;
			background-color: white;
			transition-property: background-color, transform;
			transition-duration: $animation-speed; }

		&::after {
			transform: rotate(0.25turn); } }

	// активное (подменю открыто)
	li.is-open > .nav__tab & i {
		//transform: rotate(0)

		&::before, &::after {
			transform: rotate(0); } } }

// стили стрелок навигации для разных состояний
//.nav__angle

/// ховер
///.no-touchevents li > .nav__tab &:hover i
//&::before, &::after
//	background-color: $base-color

// особые стили для пунктов ПОДМЕНЮ
.nav__drop {

	// особые стили для пунктов навигации ВТОРОГО урованя
	a {
		color: $text-color;

		@include media('>=tab-lg') {
			padding-left: 20px; }

		@include media('<tab-lg') {
			padding-left: #{round(nth($page-layout-space, 2)*2)}; } }

	// hover
	li.hover {
		// только для десктопа
		@include media('>=tab-lg') {
			& > .nav__tab a,
			& > a {
				background-color: $base-color;
				color: white; } } }

	.nav__drop {

		left: 100%;
		top: -15px;

		a {
			@include media('<tab-lg') {
				padding-left: #{round(nth($page-layout-space, 2)*3)}; } } } }

/* nav list */
// nav opener btn
a.nav-opener {
	$btn-menu-bg: white;
	$btn-menu-color: $text-color;
	$btn-menu-size: 40px;
	$btn-menu-lines-width: 30px;
	$btn-menu-lines-height: 3px;
	$btn-menu-lines-space: 5px;
	$btn-menu-lines-color: $text-color;
	$btn-menu-animation-speed: $animation-speed;
	$btn-menu-animation-delay: round-float($animation-speed*0.5, 2);

	$user-options-cross-width: $btn-menu-lines-width;
	$user-options-cross-height: $btn-menu-lines-height;
	$user-options-cross-color: $btn-menu-lines-color;

	// =====================

	display: none;
	position: fixed;
	right: 14px;
	top: round($header-height--sm/2);
	margin-top: -#{round($btn-menu-size/2)};
	z-index: 20;
	@include size($btn-menu-size);
	text-decoration: none;
	color: $text-color;
	background-color: $btn-menu-bg;
	user-select: none;
	-webkit-tap-highlight-color: transparent;

	@include media('<tab-lg') {
		display: block; }

	.touchevents & {
		// убрать подсветку при клике на тачскринах
		user-select: none;
		-webkit-tap-highlight-color: transparent; }

	& > span {
		@include hide-text;
		white-space: normal; }

	// крест
	&::before, &::after {
		@include pseudo();
		@include size($user-options-cross-width, $user-options-cross-height);
		background-color: $user-options-cross-color;
		@include position(50%);
		@include margin($top: -#{round($user-options-cross-height*0.5)}, $left: -#{round($user-options-cross-width*0.5)});
		will-change: opacity, transform;
		opacity: 0;
		transition: {
			property: transform, opacity;
			duration: $btn-menu-animation-speed;
			delay: 0s;
			timing-function: cubic-bezier(0.65, 0.05, 0.36, 1); } }

	// бутерброд
	& > i {
		position: absolute;
		left: 50%;
		top: 50%;
		@include size($btn-menu-lines-width, $btn-menu-lines-height);
		background-color: $btn-menu-lines-color;
		@include margin($top: -#{round($btn-menu-lines-height*0.5)}, $left: -#{round($btn-menu-lines-width*0.5)});
		backface-visibility: hidden;
		transition: {
			property: opacity;
			duration: $btn-menu-animation-speed;
			delay: $btn-menu-animation-delay; }

		&::before, &::after {
			@include pseudo();
			left: 0;
			@include size($btn-menu-lines-width, $btn-menu-lines-height);
			background-color: $btn-menu-lines-color;
			backface-visibility: hidden;
			transition: {
				property: transform;
				duration: $btn-menu-animation-speed;
				delay: $btn-menu-animation-delay; } }

		//first line menu
		&::before {
			top: -#{$btn-menu-lines-height + $btn-menu-lines-space}; }

		//third line menu
		&::after {
			top: #{$btn-menu-lines-height + $btn-menu-lines-space}; } }

	// hover
	.no-touchevents &:hover {
		//background-color: #f3f3f3

		&::before, &::after {
			background-color: $base-color; }

		& > i {
			background-color: $base-color;

			&::before, &::after {
				background-color: $base-color; } } }

	// active
	&.nav-is-open {
		background-color: #fff;

		&::before, &::after {
			transition-delay: $btn-menu-animation-delay;
			//background-color: $base-color

			opacity: 1; }

		&::before {
			transform: rotate(45deg) translateZ(0); }

		&::after {
			transform: rotate(-45deg) translateZ(0); }

		& > i {
			opacity: 0;
			transition-delay: 0s;

			&::before, &::after {
				transition-delay: 0s; }

			&::before {
				transform: translate(0, #{$btn-menu-lines-height + $btn-menu-lines-space}) translateZ(0); }

			&::after {
				transform: translate(0, -#{$btn-menu-lines-height + $btn-menu-lines-space}) translateZ(0); } } } }

// overlay
// в html добавить
// <div class="nav-overlay nav-overlay-js">&nbsp;</div>
.overlay {
	position: fixed;
	left: 0;
	top: 0;
	width: 100%;
	height: 100%;
	background-color: rgba(black, 0.35);
	z-index: 9998;
	opacity: 0;
	visibility: hidden;
	$prop: opacity, visibility;
	will-change: $prop;
	transition: {
		property: $prop;
		duration: $animation-speed; }

	&.nav-overlay {
		top: $header-height--sm; }

	&.open-only-mob {
		@include media('>=tab-lg') {
			display: none; } }

	&.#{$nav-is-open-class} {
		opacity: 1;
		visibility: visible; }

	&.filters-is-open, &.request-is-open {
		opacity: 1;
		visibility: visible; } }

/* navigation end */

/* === NAVIGATION end === */

/* === FOOTER === */
// footer layout
.footer {
	position: relative;
	z-index: $z-i-footer;
	background-color: white;
	overflow: hidden;

	.font-large {
		@include font-bold;
		font-size: 2.4rem; } }

.footer-row {
	position: relative;
	display: flex;
	justify-content: space-between;
	@include padding-vertical(10rem);
	min-height: 1px;

	@include media('<tab-lg') {
		@include padding-vertical(5rem); }

	@include media('<mob-lg') {
		display: block; } }

.footer-col {
	width: calc(100% / 6);
	// для браузеров не поддерживающих display: flex
	display: inline-block;
	//display: flex
	justify-content: flex-start;

	@include media('<mob-lg') {
		display: block;
		width: 100%;
		margin-bottom: 20px;
		padding: 0;

		&:last-child {
			margin-bottom: 0; } }

	&:nth-child(2) {
		width: calc(100% / 6 * 4); }

	&:first-child {
		@include media('>=mob-lg') {
			padding-right: nth($grid-space, 2);
			justify-content: flex-start;
			text-align: left; } }

	&:last-child {
		@include media('>=mob-lg') {
			padding-left: nth($grid-space, 2);
			justify-content: flex-end;
			text-align: right; } }

	p:last-child {
		margin-bottom: 0; } }

// copyright
.copyright {
	a {
		text-decoration: none;

		&:hover {
			color: $base-color; } } }

// footer logo
.footer-logo {
	display: block;
	width: 175px;
	max-width: 100%; }

// footer navigation
.footer-nav {
	overflow: hidden; }

.footer-nav__list {
	@include clear-spacing;

	&, & ul {
		list-style: none;
		margin: 0;
		padding: 0; }

	& {
		margin: -$bottom-space--article -#{nth($grid-space, 2)} $bottom-space--element 0; }

	&:last-child {
		margin-bottom: 0; }

	a {
		text-decoration: none;
		color: inherit;
		// transition
		$prop: color;
		transition: {
			property: $prop;
			duration: $animation-speed; }

		&:hover {
			color: $base-color; } }

	& > li {
		width: calc(100% / 4);
		display: inline-block;
		vertical-align: top;
		@include font($font-size, $line-height);
		letter-spacing: $letter-spacing;
		padding-right: nth($grid-space, 2);
		margin-top: $bottom-space--article;

		& > a {
			display: inline-block;
			vertical-align: top;
			@include font-bold;
			@include font(2.4rem, round-float(30/24, 2));

			& + ul {
				margin-top: $bottom-space--element; } }

		ul {
			& > li {
				margin-top: $bottom-space--row;

				ul {
					display: none; }

				li {
					font-size: 0.85em;
					padding-left: 1em; } } } } }

/* footer navigation end */

/* developer */
.developer {
	display: block;

	a {
		text-decoration: none;

		.no-touchevents &:hover {
			text-decoration: none; } } }

.developer__label {
	@include media('<mob-lg') {
		display: block; }

	i {
		@include media('<mob-lg') {
			display: none; } } }

.developer__logo {
