@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

/* user options */
$uo-item-size: $header-height $header-height; // h w
$uo-item-size--sm: $header-height--sm $header-height--sm; // h w
$uo-space: 20px;
$uo-space--sm: 10px;
$uo-icon-size: 20px;
$uo-icon-size--sm: 16px;

.user-options {
	position: absolute;
	right: 0;
	top: 0;
	@include clear-spacing;
	line-height: 1; }

.user-options__item {
	position: relative;
	@include display-inline-block-top;
	min-width: nth($uo-item-size, 2);
	height: nth($uo-item-size, 1);

	& > a, & > div {
		position: relative;
		display: flex;
		justify-content: center;
		align-items: center;
		height: 100%;
		padding: round($uo-space/2);
		color: inherit;
		line-height: 1;
		text-decoration: none;
		text-align: center;

		// убрать подсветку при клике на тачскринах
		.touchevents & {
			user-select: none;
			-webkit-tap-highlight-color: transparent; }

		// tooltip
		&[data-title]::before {
			content: attr(data-title);
			display: block;
			min-width: 100%;
			padding: 4px 8px;
			border-radius: 2px;
			position: absolute;
			//left: 50%
			right: 0;
			top: 100%;
			@include font-light;
			@include font(1.4rem, 20px);
			text-align: center;
			letter-spacing: normal;
			z-index: 2;
			color: white;
			background-color: $base-color;
			//border: 1px solid #666
			@include antialiased;
			//===========
			opacity: 0;
			visibility: hidden;
			$prop: opacity, transform;
			$translate: 0, -5px;
			transform: translate($translate);
			transform: translate3d($translate, 0);
			will-change: $prop;
			transition: {
				property: $prop;
				duration: $animation-speed; } }

		svg {
			display: inline-block;
			vertical-align: middle;
			@include size($uo-icon-size);

			// уменьшить размер для мобил
			@include media('<mob-lg') {
				@include size($uo-icon-size--sm); }

			& ~ span {
				padding-left: 12px;

				// скрыть текст для мобил
				@include media('<mob-lg') {
					display: none; } }

			& ~ span.hide-text {
				padding-left: 0; } }

		path {
			fill: currentColor; }

		& > span {
			@include display-inline-block-middle;
			@include font(1.6rem, 1);
			letter-spacing: normal;
			text-align: left; } }

	& > div {
		cursor: default; }

	// hover
	& > a {
		transition: {
			property: color;
			duration: $animation-speed; }

		&::after {
			content: "";
			display: block;
			position: absolute;
			bottom: 0;
			left: 0;
			width: 100%;
			border-top: 2px solid $base-color;
			will-change: opacity;
			opacity: 0;
			transition: {
				property: opacity;
				duration: $animation-speed; } }

		.no-touchevents &:hover {
			color: $base-color;

			&::after {
				opacity: 1; } } }

	& > a, & > div {
		// tooltip
		.no-touchevents &[data-title]:hover {
			&::before {
				opacity: 1;
				visibility: visible;
				$translate: 0, -50%;
				transform: translate($translate);
				transform: translate3d($translate, 0); } } }

	//&:last-child
	// > a, & > div
	//.no-touchevents &[data-title]:hover
	//	&::before
	//		$translate: 0, 0
	//		transform: translate($translate)
 }	//		transform: translate3d($translate, 0)

// social share
.user-options__item_social {
	display: none; }

// lang
.user-options__item_lang {
	//.lang-select__opener
 }	//idth: 85px

.lang-select__drop {
	position: absolute;
	left: 0;
	top: 100%;
	width: 100%;
	min-width: 80px;
	list-style: none;
	margin: 0;
	padding: 0;
	font-size: 1.6rem;
	line-height: 1;
	letter-spacing: normal;
	background-color: $header-bg;
	border: 1px solid $border-color;

	.active-item {
		display: none; }

	a {
		display: block;
		padding: 10px round($uo-space/2);
		color: inherit;
		text-decoration: none;
		white-space: nowrap;
		// transition
		$prop: color, background-color;
		transition: {
			property: $prop;
			duration: $animation-speed; } }

	// hover
	a {
		&:hover {
			color: white;
			background-color: $base-color; } } }

.lang-select__opener, .lang-select__drop a {
	span {
		& > i {
			display: inline-block;
			vertical-align: top;
			width: 16px;
			height: 12px;
			margin: 2px 12px 0 0;
			background-size: contain;
			background-repeat: no-repeat;
			background-position: center; } } }

.lang-select__opener {
	span {
		& > i {
			// скрыть для мобил
			@include media('<mob-lg') {
				display: none; } } } }

/* user options */

