.search-form {
	$bg-color: white;
	position: relative;
	display: block;
	display: flex;
	align-items: center;
	//right: 0
	//top: 0
	height: $header-height;
	background-color: $bg-color;
	//opacity: 0
	//pointer-events: none

	&::before {
		content: "";
		display: none;
		position: absolute;
		top: 0;
		right: 100%;
		height: 100%;
		width: 40px;
		margin: 0 -4px;
		pointer-events: none;
		@include linear-gradient(to left, $bg-color 0%, $bg-color 50%, rgba($bg-color, 0) 100%); }

	.search-is-open & {
		display: block;
		//display: flex
		//opacity: 1
 } }		//pointer-events: auto

.search-form__field {
	position: relative;
	height: nth($search-form--size, 1);
	width: nth($search-form--size, 2);
	margin: 0;

	@include media('<tab-lg') {
		height: nth($search-form--size--sm, 1);
		width: nth($search-form--size--sm, 2); }

	.search-is-open & {
		display: block; } }

input.search-form__input {
	@include size(100%);
	@include font(16px, 20px);
	color: inherit;
	padding: 10px 45px 9px 20px;
	height: nth($search-form--size, 1);
	border-radius: 0;
	border: 0 solid currentColor;
	border-bottom-width: 1px;
	background-color: $header-bg;
	box-shadow: none;

	&, &.error, &.input--has-value {
		box-shadow: none;
		border-color: currentColor !important; }

	&:hover, &:focus {
		border-color: $base-color !important; } }

.search-form__btn {
	position: absolute;
	right: 10px;
	top: 50%;
	margin: -15px 0 0;
	@include font-normal;
	@include size(30px);
	color: $text-color;
	text-align: center;
	border: none;
	background: none;
	padding: 0;
	z-index: 5;
	// transition
	$prop: color;
	will-change: $prop;
	transition: {
		property: $prop;
		duration: $animation-speed; }

	input {
		@include cover-abs();
		padding: 0;
		border: 0;
		opacity: 0;
		z-index: 99; }

	svg {
		display: inline-block;
		position: absolute;
		@include position(50%);
		@include size(18px);
		margin: -9px 0 0 -9px; }

	path {
		fill: currentColor;
		transition: inherit; }

	span {
		@include hide-text; }

	.no-touchevents &:hover {
		color: $base-color; } }

.search-opener {
	&, .user-options__item & {
		display: none;
		position: absolute;
		right: 0;
		top: 0;
		height: $search-form--opener-size;
		width: $search-form--opener-size;
		background-color: white;
		color: $text-color;
		z-index: 20;
		$prop: color, border-color;
		transition: {
			property: $prop;
			duration: $animation-speed; }

		@include media('<tab-lg') {
			top: round($header-height--sm/2); }

		svg {
			display: block;

			&.svg-ico-search {
				@include size(24px); } }

		path {
			fill: currentColor;
			transition: inherit; }

		// hover
		.no-touchevents &:hover {
			color: $base-color; }

		// active
		&.search-is-open {
			display: none; } } }

