.search-closer {
	&, .user-options__item & {
		display: none;
		position: absolute;
		right: nth($page-layout-space, 2);
		top: round($header-height/2);
		margin: -#{round($search-form--opener-size/2)} 0 0;
		height: $search-form--opener-size;
		width: $search-form--opener-size;
		color: $text-color;
		z-index: 20;
		$prop: color;
		transition: {
			property: $prop;
			duration: $animation-speed; }

		@include media('<tab-lg') {
			top: round($header-height--sm/2);
			right: 10px;
			border: none; }

		svg {
			display: block;
			position: absolute;
			@include position(50%);
			@include size(24px);
			margin: -12px 0 0 -12px; }

		path {
			fill: currentColor;
			transition: inherit; }

		// hover
		.no-touchevents &:hover {
			color: $base-color; }

		// active
		&.search-is-open {
			display: block; } } }

